var mapHandle = {
  getMap () {
    return window.$M
  },
  // 检测地图加载状态
  checkLoadState (callbcak) {
    if (window.$M === undefined) {
      setTimeout(() => {
        this.checkLoadState(callbcak)
      }, 500)
    }
    if (window.$M !== undefined) {
      callbcak()
    }
  },
  // 切换地图
  switchMapServer: function (mode) { //  1 矢量 2 影像 4 矢影
    mapHandle.getMap().switchMapServer(mode)
  },
  // 测距测面
  zoomMeterage: function (mode) { // measure 测距  measureArea 测面
    mapHandle.getMap().zoomMeterage(mode)
  },
  // 放大
  zoomIn: function () {
    mapHandle.getMap().zoomIn()
  },
  // 缩小
  zoomOut: function () {
    mapHandle.getMap().zoomOut()
  },
  // 绘制图形
  drawGraphic: function (option) {
    if (option) {
      var defaultOption = {
        // sid: '0001',
        tagert: null,
        // text: "新建信息点",
        url: '#',
        mode: 1, //  1 point, 2 line, 3 polygon, 4 rectangle, 9 circle
        isWindow: false,
        isCenter: false,
        layerId: 'glayer',
        click: '',
        onsuccess: '',
        // ico: { name: "Images/marker/marker.png", height: 30, width: 22 },
        style: {
          color: '#000000',
          fontSize: 12,
          lineStyle: 'solid',
          opacity: 0.8,
          borderWidth: 1,
          borderColor: '#000000',
          background: '#ffffff',
          fillStyle: 'solid' // backward-diagonal,cross,diagonal-cross,forward-diagonal,horizontal,none,solid,vertical
        }
      }
      mapHandle.expand(defaultOption, option)
      mapHandle.getMap().listenerDrawGraphic(defaultOption)
    }
  },
  // 叠加点
  drawPointToMap: function (option) {
    if (option) {
      var defaultOption = {
        tagert: null,
        text: '',
        url: '#',
        // x: 0,
        // y: 0,
        isCenter: false,
        layerId: 'glayer',
        click: '',
        onsuccess: ''
        // ico: { name: cremapHandle.getMap().ExtendObj.baseDir + "Images/marker/marker.png", height: 30, width: 22 }
      }
      mapHandle.expand(defaultOption, option)
      this.checkLoadState(() => {
        mapHandle.getMap().drawPointToMap(defaultOption)
      })
    }
  },
  // 叠加圆
  drawCircle: function (option) {
    var defaultOption = {
      sid: '0001',
      tagert: null,
      // text: "新建信息点",
      url: '#',
      // x: 0.0,
      // y: 0.0,
      radius: option.radius, // /米
      isCenter: false,
      //  layerId: "glayer",
      layerId: 'glayer',
      click: '',
      onsuccess: '',
      style: {
        color: '#000000',
        fontSize: 12,
        lineStyle: 'solid',
        borderWidth: 1,
        borderColor: '#000000',
        background: '#ffffff',
        fillStyle: 'dash'
      }
    }
    mapHandle.expand(defaultOption, option)
    this.checkLoadState(() => {
      mapHandle.getMap().drawCircle(defaultOption)
    })
  },
  // 叠加面
  drawPolygon: function (option) {
    var defaultOption = {
      sid: '0001',
      tagert: null,
      text: '',
      url: '#',
      pointlist: '', // // 也可以是WKT
      isCenter: false,
      layerId: 'glayer',
      click: '',
      onsuccess: '',
      style: {
        color: '#000000',
        fontSize: 12,
        lineStyle: 'solid',
        opacity: 0.8,
        borderWidth: 1,
        borderColor: '#000000',
        background: '#ffffff',
        fillStyle: 'solid'
      }
    }
    mapHandle.expand(defaultOption, option)
    mapHandle.getMap().drawPolygon(defaultOption)
  },
  drawLine: function (option) {
    var defaultOption = {
      sid: '0001',
      tagert: null,
      text: '',
      url: '#',
      pointlist: '', // // 也可以是WKT
      isCenter: false,
      layerId: 'glayer',
      click: '',
      onsuccess: '',
      style: {
        color: '#000000',
        fontSize: 12,
        lineStyle: 'solid',
        opacity: 0.8,
        borderWidth: 1,
        borderColor: '#000000',
        background: '#ffffff',
        fillStyle: 'solid'
      }
    }
    mapHandle.expand(defaultOption, option)
    mapHandle.getMap().drawLineToMap(defaultOption)
  },
  // 清除图层
  // 清除全图参数：""
  // 清除指定图层参数{ layerId: '', onsuccess: "" }
  clearLayers: function (option) {
    this.checkLoadState(() => {
      mapHandle.getMap().clearLayers(option)
    })
  },
  // 定中
  // 参数：{ x: x, y: y, level: level }
  setViewCenter: function (option) {
    this.checkLoadState(() => {
      mapHandle.getMap().setViewCenter({ x: option.x, y: option.y, level: option.level })
    })
  },
  /**
  *  多点平滑移动 v0.0.1
  *  主要应用场景：警力上图等实时gps 等场景
  *  注意：目前仅支持1个动态滑动图层，请勿设置修改 layerId ！
  **/
  pointsSlide: function (option) {
    var defaultOption = {
      layerId: 'realLayers', // 图层id ，请勿修改
      speed: 2, // 速度，值越小，行进越快
      ico: { // 图标
        // url: "assets/car1.png",
        // width: "40px",
        // height: "29px"
      },
      points: [ // 点集合
        // {
        // id: "11",                     // 唯一编号，很重要
        // x: 113.85891685966826,
        // y: 34.5658065000758
        // , ico: {
        //     url: "assets/car.png",
        //     width: "29px",
        //     height: "40px"，
        //     isAngle: false            // 是否显示方向
        // }
        //  }
      ],
      url: '#', // 弹窗内容，可以为http 链接
      isWindow: false // 是否显示弹窗
    }
    mapHandle.expand(defaultOption, option)
    mapHandle.getMap().pointsSlide(defaultOption)
  },
  // 热力图
  heatMap: function (options) {
    var defaultOption = {
      pointlist: [],
      layerId: 'heatmaplayer',
      style: {
        colors: [
          { color: 'rgba(63, 40, 102, 0)', ratio: 0 },
          { color: 'rgb(0,0,255)', ratio: 0.25 },
          { color: 'rgb(0,255,0)', ratio: 0.55 },
          { color: 'yellow', ratio: 0.85 },
          { color: 'rgb(255,0,0)', ratio: 1 }
        ],
        blurRadius: 15,
        maxPixelIntensity: 25
      },
      onsuccess: ''
    }
    mapHandle.expand(defaultOption, options)
    mapHandle.getMap().builderHeatLayer(defaultOption)
  },
  // 自定义图层
  constslayer: {
    defaultLayer: 'glayer', // 公共地图图层名称
    realPoliceLayer: 'realPoliceLayer', // 实时警力图层名称
    realCarLayer: 'realCarLayer', // 实时车辆图层名称
    cacheLayer: 'cacheLayer', // 缓冲区图层
    alarmLayer: 'alarmLayer', // 案件图层
    gridLayer: 'gridLayer', // 网格图层
    videoLayer: 'videoLayer', // 视频图层
    addressLayer: 'addressLayer', // 地址图层
    rimPoliceLayer: 'rimPoliceLayer', // 周边查询警力图层
    caseplotLayer: 'caseplotLayer', // 处置标绘图层
    caseMapLayer: 'caseMapLayer', // 警情上图图层
    policeMonitorLayer: 'policeMonitorLayer', // 历史轨迹图层
    policeMonitoPointLayer: 'policeMonitoPointLayer', // 历史轨迹点图层
    lamppostMap: 'lamppostMap' // 路灯杆图层
  },
  expand: function (source, clone) {
    if (typeof (source) === 'object' && typeof (clone) === 'object') {
      for (var attr in clone) {
        if (typeof (clone[attr]) !== 'undefined') {
          if (typeof (clone[attr]) === 'object') {
            source[attr] = this.expand(source[attr], clone[attr])
          } else {
            source[attr] = clone[attr]
          }
        }
      }
    } else {
      source = clone
    }
    return source
  }
}

export default mapHandle



