import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

// 懒加载路由
const Home = () => import('../pages/Home') // 首页
const Scheme = () => import('../pages/Scheme') // 第二页
const Command = () => import('../pages/Command') // 指挥调度
export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      name: 'home',
      path: '/home',
      component: Home
    },
    {
      name: 'scheme',
      path: '/scheme',
      component: Scheme
    },
    {
      name: 'command', // 指挥调度
      path: '/command',
      component: Command
    }
  ]
})



