<template>
  <div id="app">
    <v-header></v-header>
    <!-- 缓存路由组件 -->
    <keep-alive>
      <!-- 路由视图 -->
      <router-view />
    </keep-alive>
  </div>
</template>

<script>
import Aheader from './components/Aheader'
export default {
  name: 'App',
  components: {
    'v-header': Aheader
  }
}
</script>

<style lang="stylus">
@import './assets/stylus/mixins.styl';
@import './assets/stylus/react.css';
@import './assets/fonts/font.css';
#app {
  height: 100%;
  width: 100%;
  // min-width 1400px
  // min-height 750px
  // // background url('./assets/images/home/background.png')
  // background-repeat no-repeat
  // background-position center center
  // background-size 100% 100%
  overflow: hidden;
  pointer-events: none;
}
</style>



