import Stomp from 'stompjs'

const creWebSocket = {
  wsObj: '',
  clientObj: '',
  msConfig: {
    base: {
      rabbitmqServer: '1.119.195.59:7002',
      rabbitmqUser: 'zhdd',
      rabbitmqPass: 'zhdd',
      appid: 'zhdd'
    },
    info: {
      xzqh: '',
      unit: '',
      person: ''
    }
  },
  init: function (msConfigInfo, callback) {
    // 判断是否在应用平台中
    if (typeof (MapMethod) === 'object') { return }
    Object.assign(this.msConfig.info, msConfigInfo)
    // this.msConfig.info = msConfigInfo
    if ('WebSocket' in window) {
      this.wsObj = new WebSocket('ws://' + this.msConfig.base.rabbitmqServer + '/ws')
    }
    // 初始化 ws 对象
    // if ("WebSocket" in window) {
    //     CreWebSocket.wsObj = new WebSocket("ws://" + this.msConfig.rabbitmqServer + "/ws");
    // }
    // else {
    //     CreWebSocket.wsObj = new SockJS("http://" + this.msConfig.rabbitmqServer + "/stomp");
    // }

    /// 此处需加载到配置中心
    if (this.wsObj !== '') {
      this.clientObj = Stomp.over(this.wsObj)
      this.clientObj.heartbeat.outgoing = 30000
      this.clientObj.heartbeat.incoming = 0
      this.clientObj.connect(this.msConfig.base.rabbitmqUser, this.msConfig.base.rabbitmqPass, this.onConnect, this.onError, '/')
      this.clientObj.debug = false // 如需调试输出，直接注释此行
    }
    if (typeof callback === 'function') {
      callback()
    }
  },
  onConnect: function () {
    creWebSocket.onOpen()
  },
  onError: function () {
    creWebSocket.clientObj.disconnect()
    creWebSocket.init(creWebSocket.msConfig)
  },
  // 接收新消息—个人
  onMessagePerson: function (msg) {
    if (typeof (creWebSocket.acceptMsg) === 'function') {
      creWebSocket.acceptMsg(msg)
    }
  },
  // 接收新消息—单位
  onMessageUnit: function (msg) {
    if (typeof (creWebSocket.acceptMsg) === 'function') {
      creWebSocket.acceptMsg(msg)
    }
  },
  acceptMsg: _msg => { },
  // 连接成功
  onOpen: function () {
    // 默认订阅 单位、个人 交换机
    var unitExchange = '/exchange/' + creWebSocket.msConfig.info.xzqh + '_MobileUnit/' + creWebSocket.msConfig.info.unit
    var userExchange = '/exchange/' + creWebSocket.msConfig.info.xzqh + '_MobilePerson/' + creWebSocket.msConfig.info.person
    this.clientObj.subscribe(unitExchange, function (data) {
      var arr = data.headers.destination.split('/')
      if (arr[3] === creWebSocket.msConfig.info.unit) {
        let msg = data.body
        creWebSocket.onMessageUnit(msg)
      }
      // if (arr[3] === (JSON.parse(sessionStorage.getItem('caseInfo'))).caseId) {
      //   let msg = data.body
      //   creWebSocket.onMessageUnit(msg)
      // }
    })
    this.clientObj.subscribe(userExchange, function (data) {
      var msg = data.body
      creWebSocket.onMessagePerson(msg)
    })
  },
  // 发送消息 基础
  sendMsg: function (msg, exchange) {
    // 判断是否在应用平台中
    // if (typeof (MapMethod) === 'object') {
    //   MapMethod.SendMessage(exchange, '', msg)
    // } else {
    creWebSocket.clientObj.send(exchange, {}, msg)
    // }
  },
  // 发送消息 定制
  send: function (msg, revice, type) {
    if (type === 'person') {
      let exchange = '/exchange/' + creWebSocket.msConfig.info.xzqh + '_MobilePerson/' + revice
      creWebSocket.sendMsg(msg, exchange)
    }
    if (type === 'unit') {
      let exchange = '/exchange/' + creWebSocket.msConfig.info.xzqh + '_MobileUnit/' + revice
      creWebSocket.sendMsg(msg, exchange)
    }
    // if (type == 'toweb') {
    //   if (typeof (MapMethod) === 'object') {
    //     MapMethod.WebToWebMessage(JSON.stringify(msg))
    //   }
    // }
    // if (type == 'person' || type == 'unit' || type == 'toweb') {

    //   var msgData = JSON.parse(msg);
    //   var nowDate = new Date();
    //   var nowDateStr = nowDate.getFullYear() + '-' + getDate(nowDate.getMonth() + 1) + '-' + getDate(nowDate.getDate()) + ' ' + getDate(nowDate.getHours()) + ':' + getDate(nowDate.getMinutes()) + ':' + getDate(nowDate.getSeconds());

    //   var App_SystemMsgTextTemplate = '<p style="margin:0 auto;width:200px;color:#778899">' + nowDateStr + '</p>'
    //     + '<div  style="text-align:right;">'
    //     + '发送>>>>><br/><span>'
    //     + msgData.BusinessKey
    //     + ':业务类型'
    //     + '</span><br>'
    //     + '<span>' + msgData.BusinessId + ':业务编号'
    //     + '</span>'
    //     + '</div><br/>';

    //   document.getElementById("dv_MsgBody").insertAdjacentHTML('beforeend', App_SystemMsgTextTemplate);
    // }
  }
}

export default creWebSocket



